import React from "react";
//import { Nav } from "react-bootstrap";
import {Link} from "react-router-dom";

const YearLink = (year) => {
  return (
    <button type="button" className="btn btn-primary">
      <Link to={`/years/${year.year}`}>{year.year} </Link>
    </button>
  );
};

export default YearLink;
