import React from "react";
import { Link } from "react-router-dom";

const ShowLink = ({ show }) => {
  return (
    <button type="button" className="btn btn-primary shows">
      <Link to={"/shows/" + show.date} className="nav-link">
        {show.date} {show.venue.name}
      </Link>
    </button>
  );
};

export default ShowLink;
